import { AppRouterModule } from './app-router/app-router.module';
import { AuthInterceptor } from './auth-interceptor.service';
import { AuthService } from './services/auth.service';
import { StorageService } from './services/storage.service';
import { UserService } from './services/user.service';
import { TitleCasePipe } from './pipes/title-case.pipe';
import { ArticleService } from './services/article.service';
import { ForumComponent } from './forum/forum.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { MatInputModule } from '@angular/material/input';

import { AppComponent } from './app.component';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { BsNavbarComponent } from './bs-navbar/bs-navbar.component';
import { HomeComponent } from './home/home.component';
import { ForumsPortalComponent } from './forums-portal/forums-portal.component';
import { ForumService } from './services/forum.service';
import { ForumListComponent } from './forum-list/forum-list.component';
import { ForumCategoriesComponent } from './forum-categories/forum-categories.component';
import { ZippyComponent } from './Reusable/zippy/zippy.component';
import { ForumArticleComponent } from './Article_Page/forum-article/forum-article.component';
import { CommentListComponent } from './Article_Page/comment-list/comment-list.component';
import { CommentService } from './services/comment.service';
import { LoginComponent } from './login/login.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgForm, FormsModule } from '@angular/forms';
import { CommentFormComponent } from './Article_Page/comment-form/comment-form.component';
import { LoginRedirectComponent } from './login-redirect/login-redirect.component';
import { BlogPortalComponent } from './Blog_Section/blog-portal/blog-portal.component';
import { MyCommentsComponent } from './User-Area/my-comments/my-comments.component';
import { MyProfileComponent } from './User-Area/my-profile/my-profile.component';
import { ManageUsersComponent } from './moderator/manage-users/manage-users.component';
import { DateDisplayPipe } from './pipes/date-display.pipe';
import * as moment from 'moment';
import { SummaryPipe } from './pipes/summary.pipe';
import { ArticleFormComponent } from './article-form/article-form.component';

@NgModule({
  declarations: [
    AppComponent,
    BsNavbarComponent,
    HomeComponent,
    AboutUsComponent,
    ForumComponent,
    ForumsPortalComponent,
    ForumListComponent,
    TitleCasePipe,
    ForumCategoriesComponent,
    ZippyComponent,
    ForumArticleComponent,
    CommentListComponent,
    LoginComponent,
    CommentFormComponent,
    LoginRedirectComponent,
    BlogPortalComponent,
    MyCommentsComponent,
    MyProfileComponent,
    ManageUsersComponent,
    DateDisplayPipe,
    SummaryPipe,
    ArticleFormComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    MatInputModule,
    NgbModule,
    NgbModule.forRoot(),
    AppRouterModule,
  ],
  providers: [
    {
      provide: LocationStrategy,
      useClass: HashLocationStrategy
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor, multi: true
    },
    ArticleService, ForumService,
    CommentService, UserService,
    StorageService, AuthService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



