import { ArticleFormComponent } from './../article-form/article-form.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AboutUsComponent } from '../about-us/about-us.component';
import { ManageUsersComponent } from '../moderator/manage-users/manage-users.component';
import { BlogPortalComponent } from '../Blog_Section/blog-portal/blog-portal.component';
import { LoginRedirectComponent } from '../login-redirect/login-redirect.component';
import { LoginComponent } from '../login/login.component';
import { MyCommentsComponent } from '../User-Area/my-comments/my-comments.component';
import { ForumComponent } from '../forum/forum.component';
import { MyProfileComponent } from '../User-Area/my-profile/my-profile.component';
import { ForumArticleComponent } from '../Article_Page/forum-article/forum-article.component';
import { ForumsPortalComponent } from '../forums-portal/forums-portal.component';
import { HomeComponent } from '../home/home.component';

const routes: Routes = [
  { path: "about", component: AboutUsComponent },
  { path: "moderator", component: ManageUsersComponent },
  { path: "blogs", component: BlogPortalComponent },
  { path: "article/post/:id", component: ArticleFormComponent },
  { path: "articles/:id", component: ForumArticleComponent },
  { path: "login/success/:id", component: LoginRedirectComponent },
  { path: "login", component: LoginComponent },
  { path: "my/comments/:id", component: MyCommentsComponent },
  { path: "my/profile", component: MyProfileComponent },
  { path: "forums/:id", component: ForumComponent },
  { path: "forums", component: ForumsPortalComponent },
  { path: "", component: HomeComponent }

]

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
})
export class AppRouterModule { }



